nav{
    width: 100%;
    height: 70px;
    padding: 0 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: var(--dark);
}
nav > *:not(:hover){
    color: #fff!important;
}
main{
    padding: 10px;
}
.home_container{
    text-align: center;
    margin-top: 30px;
}
.home_container > h3{
    margin: 30px 0;
}
.area_container{
    margin-top: 24px;
    display: flex;
    justify-content: space-evenly;
}
.area_container > *{
    width: 200px;
    padding: 75px 0;
    border-radius: 4px;
    background-color: var(--dark);
    color: #fff;
    font-size: 18px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 14px;
}
.area_container .avatar{
    padding: 20px;
    border-radius: 100%;
    background-color: var(--light);
}